<template>
  <div>
    <Search v-on:inputSubmit="onSearch" />
    <div class="container">
      <h1>{{ heading }}</h1>
    </div>
    <Gallery :photos="photos" />
    <div class="text-center">
      <div class="btn-large grey darken-4 my-10" v-on:click="onLoadMore">
        Load more <i class="material-icons right">arrow_forward</i>
      </div>
    </div>
  </div>
</template>

<script>
import Gallery from '@/components/Gallery';
import Search from '@/components/Search';
import axios from 'axios';

export default {
  name: 'home',

  components: {
    Gallery,
    Search,
  },

  data() {
    return {
      photos: [],
      heading: 'Latest photos',
      nextPage: 1,
    };
  },

  methods: {
    onSearch(input) {
      if (input.length > 1) {
        axios
          .get('https://api.unsplash.com/search/photos?query=' + input)
          .then((res) => {
            this.photos = res.data.results;
            this.heading = input + ' photos';
          });
      }
    },

    onLoadMore() {
      axios
        .get('https://api.unsplash.com/photos?page=' + this.nextPage)
        .then((res) => {
          this.photos = this.photos.concat(res.data);

          this.nextPage = this.nextPage + 1;
        });
    },
  },

  created() {
    axios.get('https://api.unsplash.com/photos').then((res) => {
      this.photos = res.data;
      this.nextPage = 2;
    });
  },
};
</script>

